const NewsCenter = () => import('../views/NewsCenter/NewsCenter.vue');
const NewsDetail = () => import('../views/NewsCenter/NewsDetail.vue');
const LecturesList = () => import('../views/NewsCenter/LecturesList.vue');
const NoticeList = () => import('../views/NewsCenter/NoticeList.vue');

const routers = [
  {
    path: '/newsCenter',
    name: 'newsCenter',
    component: NewsCenter,
  },
  {
    path: '/newsDetail/:id',
    name: 'newsDetail',
    component: NewsDetail,
  },
  {
    path: '/lecturesList',
    name: 'lecturesList',
    component: LecturesList,
  },
  {
    path: '/noticeList',
    name: 'noticeList',
    component: NoticeList,
  },
];

export default routers;